:root {
	--nav-font-color: white;
	--nav-title-size: 2rem;
	--nav-title-weight: 600;
	--nav-font-size: 1.1rem;
	--nav-font-weight: 600;
	--nav-icon-size: 1.3rem;
	--nav-bg-color: #282c34;
	/* --body-bg-color: #F5F5F5; */
	--body-bg-color: #F5F5F0;
	/* --body-bg-color: #F3F3F3; */
	/* --body-bg-color: #e1e1e1; */
	--body-bg-color-accent: #708090;
	--project-card-font-color: white;

	/* --body-bg-color-accent: #e1e1e1; */
	/* --body-bg-color: #708090; */
	/* --project-card-font-color: black; */

	/* --body-bg-color: #282c34; */
	--body-cli-bg-color: #272622;


	--body-font-color: black;
	--body-font-size: 1.2rem;
	--body-width: 800px;
}

* {
}

body {
	margin: 0;
	padding: 0;
	min-width: 320px;

	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
	'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
	sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: var(--body-font-size);
	background-color: var(--body-bg-color);
	/* background-color: var(--body-cli-bg-color); */
	padding-bottom: 100px;
	line-height: 1.4;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
	monospace;
}

/* NavBar */

nav {
	background-color: #282c34;
	height: min-content;
	color: white;

}

.nav-container {
	width: max-content;
	padding: 2rem;
	margin: 0 auto;

}

.nav--top-section {
	font-size: var(--nav-title-size);
	font-weight: var(--nav-title-weight);
}

.webname, .nav--button {
	color: white;
	text-decoration: none;
}

.nav--bottom-section {
	font-size: var(--nav-font-size);
	font-weight: var(--nav-font-weight);
	padding: 10px 0 0 0;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-start;
}

.nav--icons {
	display: flex;
	align-items: center;
}

.nav--button-icon {
	margin: 0 0 0 15px;
	width: var(--nav-icon-size);
}

/* NavBar CLI */

.NavbarCLI {
	background-color: var(--body-cli-bg-color);
	height: min-content;
	color: white;
	width: 100%;
}

.nav-cli-container {
	/* background-color: black; */
	height: min-content;
	color: white;
	/* margin: 0 auto; */
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
	/* font-size: 2rem; */
	text-shadow: 0 0 4px white;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	/* align-items: space-between; */
	padding: 2rem;
}

.nav-cli--top-section {
	font-size: var(--nav-title-size);
	font-weight: var(--nav-title-weight);
}

.nav-cli--bottom-section {
	font-size: var(--nav-font-size);
	font-weight: var(--nav-font-weight);
	font-size: var(--nav-title-size);
	font-weight: var(--nav-title-weight);
	/* padding: 10px 0 0 0; */
	display: flex;
	flex-direction: row;
	/* gap: 1rem; */
	justify-content: space-between;
	width: 50%;
}

.nav-cli--goto {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	gap: 1rem;
}

.nav-cli--icons {
	display: flex;
	align-items: center;
	justify-content: center;
}

.nav-cli--cursor {
	background-color: white;
	/* padding: 0 0.2rem 0 0.2rem; */
	/* border-radius: 1px; */
	animation: cursor-blink 1s infinite;
	box-shadow: 0 0 4px white;
}

.webname-cli, .nav-cli--button {
	color: white;
	text-decoration: none;
}

.nav-cli--icons {
	display: flex;
	align-items: center;
}

.nav-cli--button {
	/* margin: 0 0 0 15px; */
	/* width: var(--nav-icon-size); */
	/* background-color: transparent; */
	/* box-shadow: 0 0 4px white; */
}

.nav-cli--button-icon {
	margin: 0 0 0 15px;
	width: var(--nav-icon-size);
	width: 2rem;
	/* background-color: transparent; */
	/* box-shadow: 0 0 4px white; */
}

/* NavbarSlim */

.NavbarSlim {
	background-color: #282c34;
	/* background-color: var(--body-cli-bg-color); */
	/* height: min-content; */
	color: white;
}

.nav-slim-container {
	padding: 2rem;
	/* margin: 0 auto; */
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.nav-slim--top-section {
	font-size: var(--nav-title-size);
	font-weight: var(--nav-title-weight);
}

.nav-slim--bottom-section {
	font-size: 1.5rem;
	font-weight: var(--nav-font-weight);
	/* padding: 10px 0 0 0; */
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	gap: 2rem;
}

.nav-slim--icons {
	display: flex;
	/* flex-direction: row; */
	align-items: center;
	gap: 1rem;
}

.webname-slim, .nav-slim--button {
	color: white;
	text-decoration: none;
}

.nav-slim--button-icon {
	display: flex;
	/* margin: 0 0 0 15px; */
	width: var(--nav-title-size);
}

/* Content */

.content-container {
	margin: auto;
	padding: 20px;
	max-width: var(--body-width);
	line-height: 1.6;
	text-align: justify;
}

p {
	padding: 0;
	margin: 0;
}

hr {
	margin: 3rem 0 3rem 0;
	padding: 0;
	background-color: lightgray;
	border: none;
	height: 2px;
	width: 100%;
}

/* Projects */

.Projects {
	margin: auto;
	/* margin-top: 1rem; */
	max-width: var(--body-width);
}

.cards-list {
	/* margin: auto; */
    display: flex;
	flex-direction: column;
    /* flex-wrap: nowrap; */
    gap: 2rem;
	padding-top: 5%;
    /* overflow-x: auto; */
	/* justify-content: flex-start; */
}

.card {
	background-color: var(--body-bg-color-accent);
	/* margin: 20px; */
    /* max-width: var(--body-width); */
	/* height: 400px; */
	/* height: 200px; */
    font-size: 1.3rem;
    display: flex;
    flex-direction: row;
	color: var(--project-card-font-color);
	border-radius: 25px;
	/* box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
}

.card--image-section {
	height: 100%;
	width: 50%;
	display: flex;
	align-items: center;
	padding: 3%;
	border-radius: 25px 0 0 25px;
}

.card--image {
	max-height: 100%;
	max-width: 100%;
}

.card--content-section {
	display: flex;
	flex-direction: column;
	width: 50%;
}

.card--top-section {
	padding: 10px;
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
}

.card--title {
	font-size: 1.8rem;
	font-weight: 500;
}

.card--discription {
	font-size: 1rem;
}

.card--bottom-section {
	height: 100%;
	display: flex;
	flex-direction: column;
}

.card--tech {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	gap: 10px;
	padding: 10px;
}

.card--tech-item {
	background-color: black;
	padding: 0.6rem;
	border-radius: 8px;
	font-weight: 600;
	/* font-size: 1.3rem; */
	/* font-size: 1rem; */
}

.card--tech-item-image {
	/* background-color: white; */
	padding: 2px;
	/* border-radius: 15px; */
	/* width: 3rem; */
	height: 3rem;
}

.card--links {
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	padding: 10px;
	gap: 20px;
}

.card--icon {
	background-color: white;
	padding: 8px;
	border-radius: 10px;
	width: 3rem;
}

@media (pointer: fine) {
	.card--icon {
		opacity: 0.4;
		transition: opacity 0.5s;
	}

	.card--icon:hover {
		opacity: 1;
	}
}

@media (pointer: coarse) {
	.card--icon {
		/* opacity: 0.9; */
		/* transition: opacity 0.8s, box-shadow 0.8s; */
		transition: box-shadow 0.8s;
	}

	.card--icon:hover {
		/* opacity: 1; */
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	}
}

/* ALGORITHMS START */

.Algorithms {
	padding-left: 1%;
	padding-right: 1%;
	margin: auto;
	margin-top: 2rem;
	max-width: var(--body-width);
	display: flex;
	flex-direction: column;
}

.title-algorithms {
	padding: 0;
	margin: 0 0 2rem 0;
	font-size: 2rem;
}

.algorithms--button-container {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	/* z-index: 1; */
}

.algorithms--button-active, .algorithms--button-idle {
	margin: 0px;
	border: 0;
	font-size: 1.5rem;
	color: black;
	font-weight: 600;
	border-radius: 15px 15px 0 0;
	padding: 1rem 1.5rem 0.5rem 1.5rem;
}

.algorithms--button-active {
	background-color: var(--body-bg-color);
	box-shadow: 0 8px 1px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 5px rgba(0, 0, 0, 0.19);
	/* box-shadow: 0px -1px 1px 0 rgba(0, 0, 0, 0.2), 0px -3px 3px -3px rgba(0, 0, 0, 0.09); */ 
	/* box-shadow: -8px 0px 5px -5px rgba(0,0,0,.8), 8px 0px 5px -5px rgba(0,0,0,.8); */
	clip-path: inset(-20px -20px 0 -20px);
	z-index: 99;
}

.algorithms--button-idle {
	background-color: darkgray;
	transition: background-color 0.4s ease;
}

.algorithms--button-idle:hover {
	background-color: lightgray;
}

.algorithms--container {
	border-top: 5px solid var(--body-bg-color);
	/* border-radius: 0 10px 10px 10px; */
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), -20px 8px 10px 10px rgba(0, 0, 0, 0.19);
	clip-path: inset(-20px 0 0 0);
}

/* SORTING */

#sortingCanvas {
	margin-top: 1rem;
	margin-bottom: 1rem;
	/* padding: 0 8px 0 8px; */
}

.controls {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.section--sort {
	margin: 0px 0px 10px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	gap: 5px;
	justify-content: space-between;
}

.section--generate {
	/* max-width: var(--body-width); */
	display: flex;
	align-items: center;
	background-color: lightgray;
	padding: 10px
}

.section--discription {
	max-width: var(--body-width);
}
.button--algo {
	background-color: darkgray;
	margin: 0px;
	border: 0;
	border-radius: 5px;
	padding: 8px 10px;
	font-size: 1rem;
	color: white;
}

.button--algo:hover {
	opacity: 0.7;
}

.button--algo:active {
	background-color: indianred;
}

.button-selected {
	background: indianred;
}

.button-selected:hover {
	background: indianred;
}

.button--refresh {
	padding: 5px 10px;
	border-radius: 5px;
	width: 2rem;
	height: 2rem;
	background-image: url('./images/refresh-white.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 65%;
}

.slider-container {
  width: 100%;
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.slider {
  -webkit-appearance: none;
  width: 90%;
  height: 10px;
  border-radius: 5px;
  background: white;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #008B8B;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #04AA6D;
  cursor: pointer;
}

.slider-icons {
	margin: 0;
	width: 24px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-end;
}

/* Archive */

.Archive {
	max-width: calc(var(--body-width) - 200px);
	margin: auto;

}

.archive-container {
	display: flex;
	flex-direction: column;
	gap: 10px;
	padding: 20px;
}

.archive--title {
	padding: 0;
	margin: 1rem 0 1rem 0;
}

.archive--link {
	text-decoration: none;
	font-size: 1.5rem;
	color: black;
	font-weight: 500;
}

.archive--link:visited {
	color: darkgray;
}

.archive--li {
	padding-left: 4rem;
}

@media only screen and (max-width: 720px) {
	.nav-slim-container {
		padding: 2rem;
		display: flex;
		flex-direction: row;
		align-items: center;
	}

	.nav-slim--bottom-section {
		font-size: 1.5rem;
		font-weight: var(--nav-font-weight);
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		gap: 2rem;
	}

	.nav-slim--more-menu {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: var(--nav-bg-color);
		color: var(--nav-font-color);
		font-size: var(--nav-title-size);
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		align-items: center;
		z-index: 10;
	}

	.nav-slim--button-more {
		cursor: pointer;
		border: none;
		width: 2rem;
		height: 2rem;
		background-color: transparent;
		background-image: url('./images/more-white.png');
		background-repeat: no-repeat;
		background-position: center;
		background-size: 100%;
	}
	
	.nav-slim--more-icons {
		position: fixed;
		bottom: 3rem;
		display: flex;
		/* flex-direction: row; */
		align-items: center;
		gap: 3rem;
	}

	.nav-slim--more-top {
		font-weight: var(--nav-font-weight);
		position: fixed;
		top: 2rem;
		display: flex;
		width: calc(100% - 4rem);
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
	}

	.nav-slim--more-center {
		display: flex;
		flex-direction: column;
		gap: 2rem;
		align-items: center;
	}

	#nav-slim--more-menu-button-exit {
		background-color: var(--nav-bg-color);
		background-image: url('./images/x-white.png');
		background-repeat: no-repeat;
		background-position: center;
		background-size: 100%;
		border: none;
		width: 2rem;
		height: 2rem;
	}

	.cards-list {
		flex-direction: row;
		flex-wrap: nowrap;
		overflow-x: auto;
		justify-content: flex-start;
		gap: 2rem;
		/* margin: 0; */
		padding: 8%;
		/* padding-top: 0; */
	}

	.card {
		flex-direction: column;
		font-size: 1rem;
		min-width: 90%;
		margin: 0;
		height: 100%;
	}

	.card--title {
		font-size: 1.6rem;
	}

	.card--image-section {
		min-width: 90%;
		margin: auto;

		padding: 1.5rem 0 0 0;
		border-radius: 25px 0 0 25px;
	}

	.card--image {
		width: 100%;

	}

	.card--content-section {
		height: 50%;
		width: 100%;
	}

	.card--tech-item-image {
		height: 2.4rem;
	}
}

/* ETC */

#button--settings {
	border: none;
	border-radius: 8px;
	margin: 0;
	padding: 0;
	width: 2rem;
	height: 2rem;
	position: absolute;
	top: 1rem;
	right: 1rem;
	top: 0.1rem;
	right: 0.1rem;
	opacity: 0.02;
	background-image: url('./images/gear-white.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 100%;
	background-color: transparent;
	transition: opacity 0.5s ease-in-out;
}

#button--settings:hover {
    animation: settings-icon-spin infinite 10s linear;
	opacity: 0.5;
}

@keyframes settings-icon-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.go-up-container {
	position: relative;
}

#button--go-up {
	border: none;
	border-radius: 8px;
	margin: 0;
	padding: 0;
	width: 2rem;
	height: 2rem;
	position: fixed;
	bottom: 2rem;
	right: 2rem;
	opacity: 0.5;
	background-image: url('./images/up-white.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 80%;
	/* background-color: transparent; */
	background-color:rgba(100, 100, 100, 0.7);
	transition: opacity 0.5s ease-in-out;
}

@media (pointer: fine) {
	#button--go-up:hover {
		animation: go-up-appear infinite 2s linear;
		opacity: 1;
	}
}

@keyframes go-up-appear {
	0%{
		transform: translateY(0rem);
	}
	25%{
		transform: translateY(-0.25rem);
	}
	75%{
		transform: translateY(0.25rem);
	}
	100%{
		transform: translateY(0rem);
	}
}

#settings {
	position: fixed;
	top: calc(50% - 250px);
	left: calc(50% - 250px);
	/* width: 50%; */
	/* height: 50%; */
	min-width: 500px;
	min-height: 500px;
	/* max-height: 500px; */
	background-color:rgba(100, 100, 100, 0.9);
	color: white;
	/* display: flex; */
	/* justify-content: center; */
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 8px 0 rgba(0, 0, 0, 0.19);
}

#settings-rim {
	width: 100%;
	height: 100%;
	background-color:rgba(100, 100, 100, 0.2);
	position: fixed;
	top: 0;
	left: 0;
	/* display: flex; */
	/* justify-content: center; */
	/* align-items: center; */
	z-index: 0;

}


#settings-content-container {
	/* width: 500px; */
	height: 500px;
	display: flex;
	flex-direction: column;
	/* align-content: center; */
	align-items: center;
	justify-content: center;

}

#settings-content--title {
	margin: 0;

}

#settings-content--text {
	padding: 2rem;

}

#settings-content--img {
	max-width: 3rem;

}

#settings-content-button-exit {
	background-color: transparent;
	background-image: url('./images/x-white.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 100%;
	border: none;
	width: 2rem;
	height: 2rem;
	position: absolute;
	top: 1rem;
	right: 1rem;
}

@media only screen and (max-width: 720px) {
	#settings {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color:rgba(100, 100, 100, 0.9);
		color: white;
		display: flex;
		flex-direction: column;
		justify-content: center;
		box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 8px 0 rgba(0, 0, 0, 0.19);
	}

	#settings-content-container {
		width: 100vw;
		height: 100vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

	}

	#settings-content-button-exit {
		background-color: transparent;
		border: none;
		color: white;
		font-size: 2rem;
		font-weight: 800;
		position: fixed;
		top: 1rem;
		right: 1rem;
	}
}

/* About */

.about--container {
	background-color: black;
	color: white;
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
	/* font-size: 2rem; */
	text-shadow: 0 0 4px white;
	padding: 25px 50px;
	white-space: wrap;
	/* width: 100%; */

}

.about--cursor {
	background-color: white;
	/* padidng-right: 0.4rem; */
	/* border-radius: 1px; */
	animation: cursor-blink 1s infinite;
	box-shadow: 0 0 4px white;
}

@keyframes cursor-blink {
	0% {
		opacity: 0;
	}
	50% {
		opacity: 0;
	}
	75% {
		opacity: 1;
	}
}

.about--intro {
	/* animation: about-typing 4s steps(100, end); */
	/* overflow-x: hidden; */
}

